@mixin overlay() {
  position: relative;
  &::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    @include glassMorph;
    transition: all 0.5s ease;
  }

  &:hover {
    &::after {
      opacity: 0;
    }
  }
}

@mixin glassMorph {
  backdrop-filter: blur(7px) saturate(200%);
  -webkit-backdrop-filter: blur(7px) saturate(200%);
  background-color: rgba(230, 46, 46, 0);
}

.hero-section {
  position: relative;
  &__bg {
    display: flex;
    height: 100vh;
  }
  &__item {
    flex: 1 0 20%;

    &:nth-child(1),
    &:nth-child(2),
    &:nth-child(4),
    &:nth-child(5) {
      display: none;
    }

    @include onPortable {
      &:nth-child(2),
      &:nth-child(1) {
        display: block;
      }
    }
    @include onDesktop {
      &:nth-child(1),
      &:nth-child(2),
      &:nth-child(4),
      &:nth-child(5) {
        display: block;
      }
    }

    &:nth-child(1) {
      @include overlay();
    }
    &:nth-child(2) {
      @include overlay();
    }
    &:nth-child(3) {
      @include overlay();
    }
    &:nth-child(4) {
      @include overlay();
    }
    &:nth-child(5) {
      @include overlay();
    }
  }
  &__item > img {
    user-select: none;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  &__content {
    pointer-events: none;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    &__wrapper {
      pointer-events:all;
    }
  }
}
